/* Weather.css */
.weather-container {
    text-align: center;
    padding: 20px;
    background-color: #0056b3;
    border-radius: 8px;
    box-shadow: 0 4px 7px rgba(0, 0, 0, 0.5);
    max-width: 330px;
    margin: 0 auto;
    margin-top: 50px;
  }
  
  input {
    padding: 10px;
    width: 60%;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    margin: 5px;
  }
  
  button {
    width: 40px;
    height: 40px;
    padding: 10px;
    background:url(./weather-icons/bg.jpg);
    background-size: cover;
    object-fit: contain;
    color: #fff;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s;
    font-size: 16px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .weather-info {
    margin-top: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 4px;
    color: white;
  }
  
  h2 {
    text-decoration: overline;
    font-size: 2rem;
    color: white;
  }

  .weather-info>span:nth-child(2) {
    font-size: 2.5rem;
    font-weight: bold;
  }
  .weather-info>span:nth-child(3) {
    font-size: 2rem;
    font-weight: bold;
  }
  .weather-info>span:nth-child(5) {
    font-size: 1.5rem;
    font-weight: bold;
  }
  .weather-info>span:nth-child(6) {
    font-size: 1.5rem;
    font-weight: bold;
  }
  img{
    width: 9rem;
    position: relative;
    left: 70px;
  }
  .boxes>.box>i{
    font-size: 2rem;
    padding: 10px;
  }
  .boxes{
    display: flex;
    justify-content: space-between;
  }
  .box{
    padding: 10px;
    width: 80px;
  }
  .soc>ul{
    display: flex;
    gap: 12px;
    float: right;
    margin-bottom: 0;
  }
  .soc>ul>li{
    list-style: none;
  }
  .soc>ul>li>a>i{
    font-size: 1.6rem;
   color: white;
  }
  .soc>ul>li>a>#facebook:hover{
  color: #1c99fe;
}
.soc>ul>li>a>#github:hover{
  color: rgb(217, 190, 154);
}
.soc>ul>li>a>#linkedin:hover{
  color: rgb(93, 149, 229);
}
.soc>ul>li>a>#insta:hover{
  color: #fd4766;
}